'use client';

import QuestionItem from '@/components/custom/QuestionItem';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { Button } from '@/components/ui/button';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { Category } from '../../types.d';

// A simple loading skeleton for the accordion
const QuestionsLoadingSkeleton = () => (
  <div className="space-y-4 animate-pulse">
    <div className="h-16 bg-muted rounded-lg w-full"></div>
    <div className="h-16 bg-muted rounded-lg w-full"></div>
    <div className="h-16 bg-muted rounded-lg w-full"></div>
    <div className="h-16 bg-muted rounded-lg w-full"></div>
  </div>
);

export default function QuestionsPage() {
  const { isAuthenticated, isLoading: isAuthLoading, user, logout } = useAuth();
  const router = useRouter();

  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // --- Route Protection ---
  useEffect(() => {
    // If the initial auth check is done and the user is not authenticated, redirect them.
    if (!isAuthLoading && !isAuthenticated) {
      router.push('/login');
    }
  }, [isAuthenticated, isAuthLoading, router]);

  // --- Data Fetching ---
  useEffect(() => {
    // Only fetch data if the user is authenticated.
    if (!isAuthenticated) return;

    const fetchData = async () => {
      setLoading(true);
      try {
        const response = await fetch(`http://localhost:5000/api/v1/content`);
        if (!response.ok) throw new Error('Failed to fetch questions');
        const result = await response.json();
        setCategories(result.data);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : 'An unknown error occurred'
        );
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [isAuthenticated]); // Re-fetch only when authentication status changes

  // --- Render Logic ---

  // Show a full-screen loader while checking authentication
  if (isAuthLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        Authenticating...
      </div>
    );
  }

  // After the check, if the user is not authenticated, render nothing (will be redirected).
  if (!isAuthenticated) {
    return null;
  }

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  return (
    <main className="flex flex-col items-center p-4 sm:p-8">
      <div className="w-full max-w-4xl">
        <header className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-3xl font-bold">GeekHeaven DSA</h1>
            <p className="text-muted-foreground">Welcome, {user?.name}!</p>
          </div>
          <Button variant="outline" onClick={handleLogout}>
            Logout
          </Button>
        </header>

        {loading ? (
          <QuestionsLoadingSkeleton />
        ) : error ? (
          <div className="text-center py-16 text-destructive">{error}</div>
        ) : (
          <Accordion type="single" collapsible className="w-full">
            {categories.map((category) => (
              <AccordionItem value={category._id} key={category._id}>
                <AccordionTrigger className="text-xl hover:no-underline">
                  {category.title}
                </AccordionTrigger>
                <AccordionContent>
                  <div className="space-y-3 pt-2">
                    {category.questions.map((question) => (
                      <QuestionItem key={question._id} question={question} />
                    ))}
                  </div>
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        )}
      </div>
    </main>
  );
}
